import React from 'react';
import {useState,useEffect} from 'react';
import './gma.css';
import gma1 from '../Assets/gma1.svg';
import gma2 from '../Assets/gma2.svg';
import gma3 from '../Assets/gma3.svg';
import gma4 from '../Assets/gma4.svg';
import gma5 from '../Assets/gma5.svg';
import gma6 from '../Assets/gma6.svg';
import gma7 from '../Assets/gma7.svg';
import gma8 from '../Assets/gma8.svg';

import andr from '../Assets/android.svg';
import web from '../Assets/web.svg';
import nxt from '../Assets/right-arrow.svg';
import pre from '../Assets/left-arrow.svg';
import Axios from 'axios';

function GMA() {

  const[curr,setCnt] = useState(0);

  // let url = "https://drive.google.com/uc?export=download&id=12NqlIF_ESfK4gm0JWpd4N6FNk1PyQFTC";
  let url = "";

  useEffect(() => {
    // const interval = setInterval(() => {
    // setCnt(curr=>(curr+1)%3);
    // }, 3000);
    // return () => clearInterval(interval);
    Axios.get('https://osm-server.herokuapp.com/values/getAll/getMyAppDemoLink').then((response)=>{
      console.log(response.data)
      if( response.data.length > 0 && response.data[0].value1 ){
        url=response.data[0].value1;
        console.log(url)
      }
    });
  }, []);

  var next=()=>{
    var nxIndex=(curr+1)%4;
    setCnt(nxIndex);
  }

  var prev=()=>{
    var prIndex=(curr+3)%4;
    setCnt(prIndex);
  }

    var GMARedirect=()=>{
        window.open("https://getmyapp.github.io", "_blank");
      }

    var GMAAppRedirect=()=>{
        // window.open("https://docs.google.com/uc?export=download&id=1wLE7ITBSiCInY416-grWECc2Z9sZ916-", "_blank");
        window.open(url, "_blank");
    }

    let scs=[
      {
        im1:<img className="gmascrsht" src={gma1} alt="logo" />,
        im2:<img className="gmascrsht" src={gma2} alt="logo" />
      },
      {
        im1:<img className="gmascrsht" src={gma3} alt="logo" />,
        im2:<img className="gmascrsht" src={gma4} alt="logo" />
      },
      {
        im1:<img className="gmascrsht" src={gma5} alt="logo" />,
        im2:<img className="gmascrsht" src={gma6} alt="logo" />
      },
      {
        im1:<img className="gmascrsht" src={gma7} alt="logo" />,
        im2:<img className="gmascrsht" src={gma8} alt="logo" />
      }

    ]

    var returnScs=(index)=>{
        return scs[index];
    }

  return (
    <div className="GMA">
      <div className="scrShts">
        <div role="button" onClick={prev} className="scnavButton prev">
          <img className="navIc" src={pre} alt="logo" />
        </div>
        {returnScs(curr).im1}
        {returnScs(curr).im2}
        <div role="button" onClick={next} className="scnavButton next">
            <img className="navIc" src={nxt} alt="logo" />
        </div>
      </div>
      <div className="navScrDots">
        <div role="button" onClick={prev} className="navButton prev">
          <img className="navIc" src={pre} alt="logo" />
        </div>
        {
            scs.map((val, ind) => {
              
                if(ind === curr){
                  return (
                    <div className="dot act" key={ind}>
                </div>
                  );
                }else{
                  return (
                    <div role="button" onClick={(e) => setCnt(ind, e)}  className="dot" key={ind}>
                </div>
              );
                }
                
            })
          }
          <div role="button" onClick={next} className="navButton next">
              <img className="navIc" src={nxt} alt="logo" />
          </div>
      </div>
      
      <div className="bottomPart">
        <div className="textGMA">
            <div className="Header">
                    Get My App
            </div>
            <div className="techstack">
                    TECH STACK: Nativescript, Angular, Java Spring-Boot, PostgreSQL
            </div>
            <div className="desc">
                    Personalized and customizable app to gift to your loved ones. 
                    Features Photo Slideshows, Checklist, Music playlist creator and player, Workout Creator, and a Mini Game.
            </div>
        </div>
        <div className="linkButtons">
            <div onClick={GMAAppRedirect} role="button" className="andrLink">
              <img className="linkicon" src={andr} alt="logo" />
              <div className="linkText">APK</div>
            </div>
            <div onClick={GMARedirect} role="button" className="webLink">
              <img className="linkicon" src={web} alt="logo" />
              <div className="linkText">Web</div>
            </div>
        </div>
      </div>
    </div>
  );
}

export default GMA;